










import Vue from 'vue';
import ReviewActions from '@/components/suite/review/ReviewActions.vue';
import ReviewExecutions from '@/components/suite/review/ReviewExecutions.vue';

export default Vue.extend({
  components: {
    ReviewActions,
    ReviewExecutions
  },

  props: {
    'checks': Array,
    'executions': Array,
    'isRunning': Boolean,
  },

  data: () => ({
    dialog: true,

    running: false,
    expanded: [],

    // checksModel: [],

    executionsTable: {
      search: '',
      loading: true,
      headers: [
        {text: 'Result', value: 'success'},
        {text: 'Execution date', value: 'executed'},
        {text: 'Checks executed', value: 'checksCount'},
        { text: '', value: 'data-table-expand' },
      ],
      data: [],
    },
  }),


  watch: {
    executions: {
      immediate: true,
      handler(value) {
        this.executionsTable.data = value;
      },
    },

    isRunning: {
      immediate: true,
      handler(value) {
        this.running = value;
      },
    },

  },

});
